@import "./variables";

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

body {
  background-image: url(../images/bg.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

a {
  text-decoration: none;
  color: var(--second-color);
}

.wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: rgba(0, 0, 0, 0.2);

  .login_box {
    position: relative;
    width: 450px;
    backdrop-filter: blur(20px);
    border: 2px solid var(--primary-color);
    border-radius: 15px;
    padding: 6.5em 2.5em 3em 2.5em;
    color: var(--second-color);
    box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.3);

    p {
      color: var(--error-color);
      margin: 0;
      margin-left: 10px;
      padding: 10px 0 0 0;
      border-radius: 15px;
      font-style: normal;
      font-weight: 600;
      font-size: 16px;
    }

    .login_header {
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: var(--primary-color);
      width: 140px;
      height: 70px;
      border-radius: 0 0 20px 20px;

      span {
        font-size: 30px;
        color: var(--black-color);
      }

      &::before {
        content: "";
        position: absolute;
        top: 0;
        left: -30px;
        width: 30px;
        height: 30px;
        border-top-right-radius: 50%;
        background: transparent;
        box-shadow: 15px 0 0 0 var(--primary-color);
      }

      &::after {
        content: "";
        position: absolute;
        top: 0;
        right: -30px;
        width: 30px;
        height: 30px;
        border-top-left-radius: 50%;
        background: transparent;
        box-shadow: -15px 0 0 0 var(--primary-color);
      }
    }
    .input_box {
      position: relative;
      display: flex;
      flex-direction: column;
      margin: 20px 0;

      .input_field {
        width: 100%;
        height: 55px;
        font-size: 16px;
        background: transparent;
        background-color: var(--bg-input-color);
        color: var(--black-color);
        padding-inline: 20px 50px;
        border: 2px solid var(--primary-color);
        border-radius: 30px;
        outline: none;
      }

      @media screen and (max-width: 768px) {
        .input_field {
          font-size: 14px;
        }
      }
      @media screen and (max-width: 425px) {
        .input_field {
          font-size: 12px;
        }
      }
      @media screen and (max-width: 320px) {
        .input_field {
          font-size: 10px;
        }
      }
    }

    .label {
      position: absolute;
      top: -10px;
      left: 20px;
      font-size: 14px;
      background-color: var(--primary-color);
      border-radius: 30px;
      color: var(--black-color);
      padding: 0 10px;
    }

    @media screen and (max-width: 375px) {
      .label {
        font-size: 12px;
      }
    }

    .icon {
      position: absolute;
      top: 18px;
      right: 25px;
      font-size: 20px;

      .svg-btn-show {
        cursor: pointer;
        background-color: transparent;
        border: none;
      }
      .svg-btn-show:hover {
        fill: rgb(77, 83, 143);
      }
    }
    .input_submit {
      margin-top: 10px;
      width: 100%;
      height: 50px;
      background: var(--bg-disabled-btn-color);
      font-size: 16px;
      font-weight: 500;
      border: none;
      border-radius: 30px;
      cursor: pointer;
      transition: 0.3s;

      &:hover {
        background: var(--second-color);
      }
      &:disabled {
        cursor: not-allowed;
        &:hover {
          background: var(--bg-disabled-btn-color);
        }
      }
    }
  }
}
